import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  ViewChild
} from '@angular/core';
import {
  ChartIdentifierConfig,
  SingleValueChartValue
} from '@sofico-framework/ui-kit/types';
import { Chart } from 'chart.js';
import { Changes, takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { combineLatest } from 'rxjs';

@UntilDestroy()
@Component({
  selector: 'sof-bar-chart',
  template: `
    <div class="chart-container" style="position: relative;  width:100%">
      <canvas #chart></canvas>
    </div>
  `,
  styleUrls: ['./bar-chart.component.scss']
})
export class BarChartComponent
  implements OnDestroy, OnChanges, OnInit, AfterViewInit {
  @ViewChild('chart', { static: true })
  chartElem: ElementRef<HTMLCanvasElement>;
  @Input() identifiersConfig: ChartIdentifierConfig[];
  @Input() tooltipFn: (
    label: string,
    value: string,
    identifier?: string
  ) => string;
  @Input() chartData: SingleValueChartValue[];
  @Changes('identifiersConfig') identifiersConfig$;
  @Changes('chartData') chartData$;
  private chart: Chart;
  private barChartData: any;

  constructor(private cdRef: ChangeDetectorRef) {
    this.cdRef.detach();
  }

  ngOnDestroy(): void {
    this?.chart?.destroy();
  }

  ngAfterViewInit(): void {
    const ctx = this.chartElem.nativeElement.getContext('2d');
    this.chart = new Chart(ctx, {
      type: 'bar',
      data: this.barChartData,
      options: {
        tooltips: {
          callbacks: {
            label: (tooltipItem, data) => {
              if (this.tooltipFn) {
                const label = data.labels[tooltipItem.index];
                const value = data.datasets[0].data[tooltipItem.index];
                const identifier = (data.datasets[0] as any).identifiers[
                  tooltipItem.index
                ];
                return this.tooltipFn(label as any, value as any, identifier);
              }
            }
          }
        },
        aspectRatio: 1,
        legend: {
          align: 'start',
          rtl: true
        } as any,
        responsive: true
      }
    });
  }

  ngOnChanges(): void {}

  ngOnInit(): void {
    combineLatest([this.identifiersConfig$, this.chartData$])
      .pipe(takeUntilDestroy(this))
      .subscribe(([identifiersConfig, values]) => {
        this.setData(
          identifiersConfig as ChartIdentifierConfig[],
          values as SingleValueChartValue[]
        );
      });
  }

  private setData(
    identifiersConfig: ChartIdentifierConfig[],
    values: SingleValueChartValue[]
  ): void {
    if (!values || !identifiersConfig) {
      return;
    }
    const labels = identifiersConfig.map(v => v.label);
    this.barChartData = {
      labels,
      datasets: [
        {
          data: values.map(v => v?.value),
          identifiers: identifiersConfig.map(v => v.identifier),
          backgroundColor: identifiersConfig.map(
            config => config.backgroundColor
          )
        }
      ]
    };
    if (this.chart) {
      this.chart.data = this.barChartData;
      this.chart.update();
    }
  }
}
